import {GoodsItem} from "./GoodsItem";
import {useContext} from 'react'
import {ShopContext} from "../context";

function GoodsList() {
    const {goods = [] } = useContext(ShopContext)
    if (!goods.length) {
        return <h3>Error</h3>
    }
    return (
        <div className='goods'>
            {goods.map(item => (
                <GoodsItem key={item.mainId} {...item}/>
            ))}
        </div>
    )
}

export {GoodsList}